body {
  min-width: 320px;
  margin: 0 auto;
  padding: 30px;
  background-color: #fff;
}

.drag-area,
.dropped-item {
  box-sizing: border-box;
  width: 100px;
  height: 100px;
  border: 1px solid #000;
}

.container {
  display: flex;
  margin-top: 30px;
  -moz-column-gap: 30px;
     column-gap: 30px;
}

.drop-area {
  box-sizing: border-box;
  width: calc(50% - 15px);
  min-width: 320px;
  overflow: auto;
  border: 1px dashed #000;
  aspect-ratio: 1 / 1;
}

@supports not (aspect-ratio: 1 / 1) {
  .drop-area::before {
    padding-top: 100%;
    float: left;
    content: "";
  }

  .drop-area::after {
    display: block;
    clear: both;
    content: "";
  }
}

.drop-area_grid {
  display: flex;
  flex-wrap: wrap;
  align-content: flex-start;
  padding: 10px;
  gap: 10px;
}

.drop-area_canvas {
  position: relative;
}

.dropped-item_canvas {
  position: absolute;
}
