body {
  min-width: 320px;
  background-color: #fff;
  margin: 0 auto;
  padding: 30px;
}

.drag-area, .dropped-item {
  box-sizing: border-box;
  width: 100px;
  height: 100px;
  border: 1px solid #000;
}

.container {
  -moz-column-gap: 30px;
  column-gap: 30px;
  margin-top: 30px;
  display: flex;
}

.drop-area {
  box-sizing: border-box;
  width: calc(50% - 15px);
  min-width: 320px;
  aspect-ratio: 1 / 1;
  border: 1px dashed #000;
  overflow: auto;
}

@supports not (aspect-ratio: 1 / 1) {
  .drop-area:before {
    float: left;
    content: "";
    padding-top: 100%;
  }

  .drop-area:after {
    clear: both;
    content: "";
    display: block;
  }
}

.drop-area_grid {
  flex-wrap: wrap;
  align-content: flex-start;
  gap: 10px;
  padding: 10px;
  display: flex;
}

.drop-area_canvas {
  position: relative;
}

.dropped-item_canvas {
  position: absolute;
}

